<template>
    <div>
        <!-- <div v-if="show" style="position:fixed;top:0;left:0;width:100vw;height:100vh;background:black;opacity:0.3;">

        </div>
        <div v-if="show" style="position: fixed;top: 50%;left: 50%;transform: translateX(-50%) translateY(-50%);z-index:10;">
            <div style="width:100%;" class="bg-white p-3 shadow-md rounded-lg p-3">
                <p class="text-xl p-3">
                </p>
            </div>
        </div> -->
        <div class="container" >
            <div class="row justify-content-center sm:p-4" v-if="td.length>0">
                <div class="col-12 ">
                    <br>
                    <div class="row">
                        <div class="col-sm-6">
                            <div class="input-group mb-2">
                                <div class="input-group-prepend">
                                    <div class="input-group-text"><span class="typcn typcn-pen"></span></div>
                                </div>
                                <input type="text" class="form-control py-0" id="pencarian" placeholder="Cari Bacaan Sholat ..." v-model="cari">
                            </div>
                        </div>
                        <div class="offset-sm-3 col-sm-3 pl-3 pr-3">
                            <div class=" bg-white rounded-lg p-3">
                                <span>Ukuran huruf : </span>
                                <div class="sm-form float-right">
                                    <select class='form-control text-sm' style="height:34px;width:100px;" v-model="pilihanLebar">
                                        <option v-for="(item, index) in lebar" :key="index" :value="item">{{index+1}}</option>
                                    </select>
                                </div>
                            </div>
                        </div>
                    </div>
                    <p class="p-3 text-center text-2xl font-bold font-italic">
                        BACAAN SHOLAT
                    </p>
                </div>
                <div class="col-12 col-sm-12 mt-3 cursor-pointer p-2" @click="pilih=item;show=!show;" v-for="(item, index) in td" :key="index+'asmaulhusna'">
                    <div class="rounded-lg shadow bg-white p-2 sm:p-5 ">
                        <p class="pb-5 text-2xl font-italic">
                            {{item.name}}
                        </p>
                        <p class="pb-3 text-center " :class="`${pilihanLebar}`" style="font-family: 'Amiri', serif;line-height: 2.0;">
                            {{item.arabic}}
                        </p>
                         <p class="text-sm font-italic text-center mt-2">
                            {{item.latin}}
                        </p>
                        <p class="pt-2 text-sm">
                            {{item.terjemahan}}
                        </p>
                    </div>
                </div>
                <div class="col-12">
                    <!-- <p class="p-3 text-center text-lg font-bold font-italic">Sumber : {{detail.source}} <br> <br> Based_on {{detail.based_on}}</p> -->
                </div>
            </div>
             <div v-else>
                <div style="height:90vh;" class="d-flex justify-content-center align-items-center ">
                    <div style="width:100%">
                          <p class="text-center animated bounce infinite text-lg font-semibold">
                                Loading ...
                            </p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default{
    layout:'app_islamic',
    computed:{
        td(){
            let that=this;
            let data =this.datanya;
            data=data.filter(e=>{
                if(e.name.toString().toLowerCase().indexOf(that.cari)!=-1){
                    return e;
                }
            })
            return data;
        }
    },
    data(){
        return{
            cari:'',
            datanya:[],
            detail:{},
            show:false,
            pilih:{},
             lebar:[
                'text-lg',
                'text-xl',
                'text-2xl',
                'text-4xl',
                'text-5xl',
            ],
            pilihanLebar:'text-2xl'
        }
    },
    methods: {
        getData(){
            axios.get('https://islamic-api-zhirrr.vercel.app/api/bacaanshalat').then(res=>{
                this.datanya=res.data
                // this.detail=res.data
                console.log(res.data)
                this.$forceUpdate();
            })
        }
    },
    mounted() {
        this.getData()
    },
      metaInfo: {
        title: `Baca Al Qur'an`,
        link: [
         { rel: 'preconnect', href: 'https://fonts.googleapis.com' },
         { rel: 'preconnect', href: 'https://fonts.gstatic.com' },
         { rel: 'stylesheet', href: 'https://fonts.googleapis.com/css2?family=Amiri&display=swap' },
        ],
    },
}
</script>